* {
  margin: 0;
  padding: 0;
}

.img {
  width: 3vw;
}

.header {
  display: flex;
  font-family: Pixelify Sans;
  background-color: black;
  align-items: center;
  justify-content: space-between;
  height: 70px;
  font-size: 18px;
}

.logo {
  width: 4rem;
}

.navigation {
  display: flex;
  align-items: center;
}

.controlbar {
  margin-right: 3rem;
}

.body {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
  font-family: Pixelify Sans;
  background-image: url("../img/background.jpg");
  background-size: cover;
}

.content {
  display: flex;
  background: #000000;
  box-shadow: 0px 0px 47px 0px rgba(0, 0, 0, 0.75);
  margin-left: 5%;
  max-width: 1240px;
  color: white;
}

.form {
  width: 100%;
  background: white;
  color: black;
  padding: 30px;
}

.label {
  margin: 10px 0 10px 0;
}

.header__reference {
  text-decoration: none;
  color: white;
  margin-left: 1rem;
}

.header__input {
  font-family: Pixelify Sans;
  height: 30px;
  font-size: 18px;
  padding-left: 10px;
}

.content__text {
  display: flex;
  flex-direction: column;
  margin: 2%;
  justify-content: center;
}

.content__title {
  font-size: 100px;
}

.content__filling {
  font-size: 20px;
  line-height: 28px;
  color: #afafaf;
}

.label__inputName {
  margin: 0 0 10px 0;
}

.label__input {
  width: 95%;
  height: 40px;
  border: black solid 2px;
  padding-left: 5%;
  font-family: Pixelify Sans;
  font-size: 20px;
}

.form__button {
  font-family: Pixelify Sans;
  font-size: 20px;
  width: 100%;
  height: 60px;
  background: black;
  color: white;
  border: none;
  margin: 10px 0 10px 0;
}

.label__passwordDescription {
  margin: 10px 0 0 0;
}

.header__reference-whiteborder {
  border: 2px solid white;
}

.header__reference-pdhowbtn {
  padding: 3px 10px 3px 10px;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
  margin: 0;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

